body{overflow-x:hidden}.App{text-align:center;min-height:100vh;width:100vw;background-color:#ffffff;color:#2e2e2e;font-family:"Chakra Petch";overflow-y:auto}.App button{border-radius:50px}.App p{font-size:20px}.App h4{font-size:30px;margin-bottom:.5em;font-weight:600}.App h3{font-size:40px;margin-bottom:.5em;font-weight:600}.App h2{font-size:60px;margin-bottom:.5em;font-weight:600}.App h1{margin-bottom:.3em}.App button{box-shadow:0px 8px 15px rgba(0,0,0,0.1)}

.Home{text-align:center;min-height:100vh}.Home h1{font-weight:600;font-size:6rem}.Home button{width:100px;box-shadow:0 25px 50px -12px rgba(0,0,0,0.25);border:0px !important}.Home .apply-button{transition-duration:0.15s}.Home .apply-button:hover{transform:scale(1.05)}.Home .apply-button-wrapper{display:block;margin-right:auto}@media screen and (max-width: 900px){.Home .apply-button-wrapper{margin-right:0}}.Home .wave-border{width:100vw}.Home .banner{position:absolute;width:100vw}@media screen and (max-width: 500px){.Home .banner{display:hidden}}@media screen and (max-width: 900px){.Home .banner{margin-top:5rem}}.Home .glow{color:#fff;text-align:center;text-shadow:0 0 50px #fff}.Home .sin-wave--one{margin-bottom:-1px}.Home .sin-wave--two{margin-top:-1px}.Home .card{color:white;width:60vw;background:linear-gradient(to right, #A23FD1, #191970);padding:3rem;border-radius:1rem;box-shadow:0 30px 40px rgba(205,106,215,0.54);z-index:1}@media screen and (max-width: 500px){.Home .card{width:80vw;text-align:center;padding:1.7rem;padding-top:2rem}}@media screen and (max-width: 900px){.Home .card{margin-top:5rem}}.Home .card h2{text-align:center;font-size:3rem}@media screen and (max-width: 500px){.Home .card h2{font-size:25px}}.Home .card-text{text-align:left}.Home #intro{height:100vh;position:relative;display:flex;justify-content:space-between;align-items:center;flex-direction:column;text-align:left;color:white;box-shadow:inset 0px 0px 15px rgba(0,0,0,0.1);background:linear-gradient(to right, #A23FD1, #191970)}.Home #intro .intro-wrapper{display:flex;align-items:center;justify-content:center;width:100%;height:calc(100% - 320px)}.Home #intro .schedule-nav{position:absolute;top:0;right:0;display:flex;justify-content:flex-end}.Home #intro .schedule-nav a{margin-right:0 !important;text-decoration:none;display:flex;align-items:center}.Home #intro .schedule-nav a h5{margin-bottom:0;margin-left:30px}.Home #intro .schedule-button{margin:2em;box-shadow:0px 8px 15px rgba(0,0,0,0.1)}.Home #intro .title-info{display:flex;flex-direction:row;margin-top:15rem}@media screen and (max-width: 900px){.Home #intro .title-info{flex-direction:column-reverse;align-items:center;text-align:center;justify-content:center;margin-top:20rem}.Home #intro .title-info h1{font-size:60px}.Home #intro .title-info .title-info-description{width:70vw}}.Home #intro .title-info .hero-wrapper{display:flex;flex-direction:column;justify-content:space-between;z-index:3}.Home #intro .title-info .claw-machine-wrapper{position:relative;height:18rem;padding:0 3%;z-index:2}@media screen and (max-width: 900px){.Home #intro .title-info .claw-machine-wrapper{position:relative;transform:scale(0.75);top:20px;margin-bottom:30%;z-index:2}}@media screen and (max-width: 500px){.Home #intro .title-info .title-info{width:100vw}.Home #intro .title-info .claw-machine-wrapper{transform:scale(0.5);margin-bottom:0}}.Home #intro .title-info .claw-machine{height:500px}.Home #intro .title-info .claw-machine-back{z-index:10;position:absolute}.Home #intro .title-info .claw-and-anteater{z-index:2;position:absolute;height:500px;padding:30px 30px}.Home #intro .title-info .succulent{width:424px;height:400px;margin-left:2rem}.Home #intro .title-info div{display:flex;flex-direction:column;justify-content:center}.Home #intro .title-info h1{margin-bottom:.1em !important}@media screen and (max-width: 900px){.Home .wave-transition{display:none}}@media screen and (max-width: 900px){.Home .card p{text-align:center;font-size:1rem}.Home .card h2{font-size:2rem}}.Home #info{margin:7rem 0 0 0;display:flex;justify-content:center;align-items:center;flex-direction:column;position:relative}.Home #info .card{background:linear-gradient(270deg, #191970 0%, #7655BC 100%),linear-gradient(270deg, #A4C7EF 0%, #EBFBFF 100%) !important;box-shadow:0px 50px 100px 0px rgba(134,47,156,0.4)}.Home #info .zothacks-logo{width:100px;margin:0 auto 2rem auto}.Home #mentors{display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative}.Home #mentors .card{box-shadow:0px 50px 100px 0px rgba(205,106,215,0.54)}.Home #mentors>h2{margin-top:10vh;width:80vw}.Home #mentors div{display:flex;flex-direction:row;justify-content:space-between}@media screen and (max-width: 900px){.Home #mentors div{flex-direction:column;align-items:center}}.Home #mentors div img{width:18vw;height:18vw;border-radius:1rem;margin-right:1rem}@media screen and (max-width: 900px){.Home #mentors div img{width:15rem;height:15rem;margin-right:0}}.Home #mentors div .mentor-information{margin-left:1.5rem;display:flex;justify-content:center;flex-direction:column;text-align:left}.Home #mentors div .mentor-information h2{text-align:left}@media screen and (max-width: 900px){.Home #mentors div .mentor-information{margin:2rem 0 1rem 0;text-align:center;align-items:center}.Home #mentors div .mentor-information h2{text-align:center}}.Home #mentors .mentors-butterfly{width:100px;height:100px}.Home #mentors .--one{width:120px;height:120px;margin-right:70vw;margin-bottom:-1px;z-index:2;transform:translateY(190px)}@media screen and (max-width: 900px){.Home #mentors .--one{position:absolute;top:23px;left:21vw;margin:0;transform:translateX(-50px)}}@media screen and (max-width: 500px){.Home #mentors .--one{display:none}}.Home #mentors .--two{margin-left:auto;margin-right:8vw;transform:translateY(-150px) scale(1.5)}@media screen and (max-width: 900px){.Home #mentors .--two{position:absolute;top:630px;right:13vw;margin:0;transform:translateX(40px)}}@media screen and (max-width: 500px){.Home #mentors .--two{display:none}}.Home #sponsors{display:flex;align-items:center;justify-content:center;flex-direction:column;margin-bottom:7rem;width:100vw;text-align:left}.Home #sponsors>h2{margin-top:2rem}.Home #sponsors .card{background:linear-gradient(to right, #76A9E5, #466FD9);box-shadow:0px 50px 100px 0px rgba(77,122,163,0.57)}.Home #sponsors .sponsor-tier{font-size:1.8rem;border-bottom:2px solid white;text-align:left}.Home #sponsors .sponsor-logo-wrapper{width:100%;display:flex;align-items:center;justify-content:space-evenly;flex-wrap:wrap;margin:2rem auto}.Home #sponsors .sponsor-logo-wrapper>*{text-align:center;width:100%}.Home #sponsors .sponsor-logo-wrapper a{text-align:center}.Home #sponsors .sponsor-logo-wrapper .sponsor-one{margin:0 1rem;padding-bottom:3rem;width:40%;height:40%}.Home #sponsors .sponsor-logo-wrapper .sponsor-one:hover{transform:scale(1.05)}.Home #sponsors .sponsor-logo-wrapper .sponsor-two{margin:0 1rem}.Home #sponsors .sponsor-logo-wrapper .sponsor-two:hover{transform:scale(1.05)}.Home #faq{color:white;display:flex;align-items:center;justify-content:space-between;flex-direction:column;background:linear-gradient(to right, #A23FD1, #191970)}.Home #faq>h2{width:80vw;margin-top:2rem}@media screen and (max-width: 900px){.Home #faq>h2{margin-top:3rem}}.Home #faq>div{width:60vw}@media screen and (max-width: 500px){.Home #faq>div{width:80vw}}.Home #credits{margin:-1px 0;height:15vh;display:flex;justify-content:center;align-items:center;color:white;background-color:#AFE3F9;background:linear-gradient(to right, #A23FD1, #191970)}.Home #credits p{margin:0}@media screen and (max-width: 900px){.sponsor-two{width:40%}}@media screen and (max-width: 500px){.star-banner{display:none}.ticket-banner{display:none}.sponsor-two{width:30%}}

#app-navbar{width:100%;position:fixed;z-index:100;background:rgba(8,8,77,0.8);-webkit-filter:drop-shadow(0px 7px 12px rgba(0,0,0,0.35));filter:drop-shadow(0px 7px 12px rgba(0,0,0,0.35))}#app-navbar .navbar-nav{align-items:center}#app-navbar .nav-item{margin:1rem}#app-navbar .nav-link{font-size:1.5rem;font-weight:700;padding:0.35rem 1rem}#app-navbar .nav-link-outline{border:2px solid #ff8dcb;border-radius:0.5rem;color:#ff8dcb;transition:background-color 0.35s ease, color 0.35s ease}#app-navbar .nav-link-outline:hover,#app-navbar .nav-link-outline.active{background-color:#ff8dcb;color:#2e2e2e}#app-navbar .navbar-logo{height:40px;width:40px}@media screen and (min-width: 768px){#app-navbar .navbar-logo{height:60px;width:60px}#app-navbar .nav-link{font-size:1.75rem}}

