.numbers-group{display:flex;flex-direction:column;background-color:white;color:#2e2e2e;width:20vw;height:450px;box-shadow:0px 8px 15px rgba(0,0,0,0.1);border-radius:20px}@media screen and (max-width: 900px){.numbers-group{width:80vw}}.numbers-group div{margin-top:3.5em !important;margin:2em;height:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}.numbers-group p{font-size:18px}.numbers-group i{font-size:5em;margin-bottom:.5em}

.faq-collapse{text-align:left;margin-bottom:2rem}.faq-collapse svg{cursor:pointer}.faq-collapse svg:hover{color:#ededed}.faq-collapse .answer-wrapper{display:flex;flex-direction:row}.faq-collapse .answer-wrapper div{width:100%}.faq-collapse .answer-wrapper a{font-weight:bold;text-decoration:underline;color:white}.faq-collapse .answer-wrapper a:hover{color:white}.faq-collapse .answer-wrapper p{margin-top:1rem}.faq-collapse .faq-collapse-question{display:flex;flex-direction:row;align-items:center;justify-content:space-between;cursor:pointer}.faq-collapse .faq-collapse-divider{width:100%;height:3px;min-height:3px;border-radius:2px;background-color:white;margin-top:0.5rem}.faq-collapse h3{margin:0;font-size:24px;max-width:80%}.faq-collapse p{padding:0 1rem;width:90%;margin:auto}@media screen and (max-width: 900px){.faq-collapse{margin-bottom:1.5rem}.faq-collapse-question h3{font-size:1.25rem}.answer-wrapper p{font-size:1rem}}

.external-link-list{display:flex;flex-direction:row}.external-link-list div{margin-right:20px}.external-link-button{display:flex;align-items:center;background:#53007A;border-radius:10px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:10px}.external-link-text{color:white;text-decoration:none}.external-link-text img{margin-left:10px}.external-link-text:hover{text-decoration:none;color:white}@media screen and (max-width: 800px){.external-link-list{flex-direction:column}.external-link-list div{margin-bottom:20px}}

.Schedule{text-align:center;min-height:100vh;min-width:100vw;background:linear-gradient(270deg, #191970 0%, #a23fd1 100%);display:flex;flex-direction:column;align-items:center}.Schedule h1{font-weight:600}@media screen and (max-width: 900px){.Schedule h2{font-size:3rem;margin-bottom:0}}.Schedule #schedule-nav{width:100vh;position:relative}.Schedule .schedule-pixels{margin-top:-11.5vh}.Schedule .schedule-pixels img{width:98vw}@media screen and (max-width: 900px){.Schedule .schedule-pixels{margin-top:0}}.Schedule #schedule-block{position:relative;display:flex;align-items:center;flex-direction:column;top:100px;justify-content:center;align-items:center;color:white}.Schedule #schedule-block .schedule-countdown{width:55vw;text-align:left;font-family:"Chakra Petch", sans-serif;font-style:normal;font-weight:bold;font-size:10vw;padding-top:11vh}@media screen and (max-width: 900px){.Schedule #schedule-block .schedule-countdown{margin-bottom:0 !important}}.Schedule #schedule-block .schedule-countdown-text{font-family:"Chakra Petch", sans-serif;font-style:normal;font-weight:normal;font-size:1.5vw}@media screen and (max-width: 900px){.Schedule #schedule-block .schedule-countdown-text{font-size:3vw}}.Schedule #schedule-cards{margin-top:140px;height:100%;justify-content:center;align-items:center;width:80%;background:rgba(16,0,29,0.57);margin-bottom:80px;border-radius:10px}@media screen and (max-width: 900px){.Schedule #schedule-cards{align-items:center}}.Schedule #schedule-cards .schedule-card{padding:2rem;display:flex;flex-direction:column;margin-left:2rem;margin-right:2rem}@media screen and (max-width: 900px){.Schedule #schedule-cards .schedule-card{margin-left:0;margin-right:0;margin-bottom:2rem}}.Schedule #schedule-cards .schedule-card h3{color:#fff;font-family:"Chakra Petch", sans-serif;font-style:normal;font-weight:bold}.Schedule #schedule-cards .schedule-card .schedule-card-event{float:left;padding-bottom:28px}@media screen and (max-width: 900px){.Schedule #schedule-cards .schedule-card .schedule-card-event{display:flex;flex-direction:column;align-items:flex-start;text-align:left}}.Schedule #schedule-cards .schedule-card .schedule-card-event .left-text{float:left;font-family:"Chakra Petch", sans-serif;font-style:normal;font-size:1.5rem;color:#fff}.Schedule #schedule-cards .schedule-card .schedule-card-event .right-text{color:#ed96ca;font-size:20px;float:right}

.StarterPacks{text-align:center;background:linear-gradient(270deg, #191970 0%, #A23FD1 100%);display:flex;flex-direction:column;align-items:center;color:#fff;padding-bottom:8rem}.StarterPacks ul{text-align:left}.StarterPacks ul li{font-size:1.2rem}.StarterPacks ul a{color:#2053fc}.StarterPacks h2{margin-top:8rem}.StarterPacks h4{text-align:left;margin-top:2rem}.StarterPacks p{text-align:left}.StarterPacks section{width:60vw}.StarterPacks .resources-main-title{font-size:8rem;text-shadow:3px 3px 17px rgba(255,255,255,0.4);margin-top:12rem}.StarterPacks .resource-title{display:flex;justify-content:center;align-items:center;flex-direction:row}.StarterPacks .resource-title img{padding:10px 0}.StarterPacks .resource-title h3{text-decoration:underline;margin-bottom:0 10px;padding:0 20px}@media screen and (max-width: 800px){.StarterPacks .resources-main-title{margin-top:8rem;font-size:3.8rem}.StarterPacks section{width:90vw}}@media screen and (max-width: 440px){.StarterPacks .resources-main-title{margin-top:8rem;font-size:3rem}.StarterPacks p{font-size:16px}.StarterPacks .resource-title{margin-left:auto;margin-right:auto;font-size:2rem}}@media screen and (max-width: 350px){.StarterPacks .resource-title img{display:none}}

