// Quantity should be the same in PUG
$quantity: 40;


.blinker {
  position: absolute;
  left: 40%;
  top: 50%;
  width: 0.8vw;
  height: 0.8vw;
  margin: -0.2vw 0 0 9.8vw;
  animation: ease 200s alternate infinite;
  pointer-events: none;
  z-index: 1;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    transform-origin: -10vw;
  }

  &::before {
    background: rgba(0,0,0,0.0);
    opacity: 0.4;
  }

  &::after {
    background: white;
    opacity: 0;
    box-shadow: 0 0 0vw 0vw white;
    animation: flash ease infinite;
  }
}

// Randomize Blinkers Motion
@for $i from 1 through $quantity {
  $steps: random(12) + 16;
  $rotationSpeed: random(10) + 8s;

  .blinker:nth-child(#{$i}) {
    animation-name: move#{$i};
    transform: translateX(random(100) - 50vw) translateY(random(100) - 50vh) scale(random(75) / 100 + .25);

    &::before {
      animation-duration: #{$rotationSpeed};
    }

    &::after {
      animation-duration: #{$rotationSpeed}, random(6000) + 5000ms;
      animation-delay: 0ms, random(8000) + 500ms;
    }
  }

  // @keyframes move#{$i} {
  //   @for $step from 0 through $steps {
  //     transform: translateX(random(100) - 50vw) translateY(random(100) - 50vh) scale(random(75) / 100 + .25);
  //   }
  // }
}

@keyframes drift {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes flash {
  0%,
  30%,
  100% {
    opacity: 0;
    box-shadow: 0 0 0vw 0vw white;
  }
  20% {
    opacity: 1;
    box-shadow: 0 0 2vw 0.4vw white;
  }
}